import React, { useEffect, useState } from 'react';
import { motion } from 'framer-motion';
import BottomNavbar from './BottomNavbar';
import '../styles/TelegramWebApp.css';

const TelegramWebApp: React.FC = () => {
  const [initialized, setInitialized] = useState(false);

  useEffect(() => {
    //     Telegram Mini App,    
    setInitialized(true);
  }, []);

  return (
    <motion.div 
      className="telegram-webapp-container"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <div className="telegram-header">
        <h1>Telegram </h1>
      </div>

      <div className="telegram-content">
        <div className="telegram-info-card">
          <h2> Telegram </h2>
          <ul>
            <li>   Telegram</li>
            <li>    </li>
            <li>    </li>
            <li>   </li>
          </ul>
        </div>

        <div className="telegram-connect-section">
          <p>     ""    " Telegram".</p>
        </div>
      </div>

      <BottomNavbar />
    </motion.div>
  );
};

export default TelegramWebApp; 