import React, { useState } from 'react';
import { motion } from 'framer-motion';
import BottomNavbar from './BottomNavbar';
import '../styles/SubscriptionPage.css';

interface PlanFeature {
  title: string;
  included: boolean;
}

interface SubscriptionPlan {
  id: string;
  name: string;
  price: string;
  duration: string;
  features: PlanFeature[];
  popular?: boolean;
}

const subscriptionPlans: SubscriptionPlan[] = [
  {
    id: 'free',
    name: '',
    price: '0',
    duration: '',
    features: [
      { title: ' 20 ', included: true },
      { title: ' ', included: true },
      { title: ' ', included: true },
      { title: '  ', included: false },
      { title: '  Telegram', included: false },
      { title: ' ', included: false },
      { title: ' ', included: false },
    ]
  },
  {
    id: 'premium',
    name: '',
    price: '99',
    duration: ' ',
    features: [
      { title: '  ', included: true },
      { title: ' ', included: true },
      { title: ' ', included: true },
      { title: '  ', included: true },
      { title: '  Telegram', included: true },
      { title: ' ', included: true },
      { title: ' ', included: false },
    ],
    popular: true,
  },
  {
    id: 'pro',
    name: '',
    price: '199',
    duration: ' ',
    features: [
      { title: '  ', included: true },
      { title: '  ', included: true },
      { title: ' ', included: true },
      { title: 'API ', included: true },
      { title: '  ', included: true },
      { title: ' ', included: true },
      { title: ' ', included: true },
    ]
  }
];

const SubscriptionPage: React.FC = () => {
  const [selectedPlan, setSelectedPlan] = useState<string>('free');

  const handlePlanSelect = (planId: string) => {
    setSelectedPlan(planId);
  };

  const handleSubscribe = () => {
    //     
    alert(`   ${selectedPlan}.     .`);
  };

  return (
    <motion.div 
      className="subscription-page"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <div className="subscription-header">
        <h1></h1>
        <p>   </p>
      </div>

      <div className="subscription-plans">
        {subscriptionPlans.map((plan) => (
          <motion.div 
            key={plan.id}
            className={`plan-card ${selectedPlan === plan.id ? 'selected' : ''} ${plan.popular ? 'popular' : ''}`}
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
            onClick={() => handlePlanSelect(plan.id)}
          >
            {plan.popular && <div className="popular-badge"></div>}
            <h2 className="plan-name">{plan.name}</h2>
            <div className="plan-price">
              <span className="price">{plan.price}</span>
              <span className="duration">{plan.duration}</span>
            </div>
            <ul className="plan-features">
              {plan.features.map((feature, index) => (
                <li key={index} className={feature.included ? 'included' : 'not-included'}>
                  {feature.included ? '' : ''} {feature.title}
                </li>
              ))}
            </ul>
            <button 
              className={`select-plan-btn ${selectedPlan === plan.id ? 'selected' : ''}`}
              onClick={() => handlePlanSelect(plan.id)}
            >
              {selectedPlan === plan.id ? '' : ''}
            </button>
          </motion.div>
        ))}
      </div>

      <div className="subscription-actions">
        <button 
          className="subscribe-btn"
          onClick={handleSubscribe}
          disabled={selectedPlan === 'free'}
        >
          {selectedPlan === 'free' ? '   ' : ' '}
        </button>
      </div>

      <div className="subscription-info">
        <p>   ,     .      - .</p>
      </div>

      <BottomNavbar />
    </motion.div>
  );
};

export default SubscriptionPage; 